import React, { Component } from 'react';
import {TweenMax, Power2, TimelineMax} from "gsap";

import './App.css';

class App extends Component {
  componentDidMount(){

      let text = document.getElementById('compatibility');
      TweenMax.from(text,2,{y:-50});
      TweenMax.to(text, 2, {fontSize:"50px", height:"150px"});

      }
  render() {
    return (
      <div className="App">
        <div id="compatibility">Testing gsap</div>
      </div>
    );
  }
}

export default App;



